@import url(https://fonts.googleapis.com/css2?family=Overpass&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* @import url('https://fonts.googleapis.com/css2?family=Overpass:wght@400;800&display=swap'); */

:root { 
--snow-background: #E0e8f0;
--blue-lake: #51a2d9;
--blue-sky: #53c0f0;
--ice: #b9e5f3;
--red-cabin: #8a140e;
--dark-lake: #1a57ba; 
}

body {
  font-family: 'Overpass', sans-serif!important;
  /* background-color: var(--snow-background); */
}

h4 {
  font-size: 1.1rem!important;
  font-family: 'Overpass', sans-serif;
  /* font-weight: 900; */
}

h5 {
  font-size: 1rem!important;
  font-family: 'Overpass', sans-serif;
  color:#1a57ba;
  color:var(--dark-lake)

}

.card-columns {
  padding: 0.75rem 0px!important;
}

.card {
  padding: 0.75rem;
  min-height: 385px;
}

.title-blues {
  color: #8a140e;
  color: var(--red-cabin);
}

.project-info {
  max-width: 100%!important;
}

.img-info {
  width: 100%;
}

.publication-type {
  background-color: #1a57ba!important;
  background-color: var(--dark-lake)!important;
  color: white!important;
  opacity: 1!important;
  border-color: #1a57ba!important;
  border-color: var(--dark-lake)!important;
  cursor: default!important;
}
.pub-type-title {
  text-align: center;
  padding: 8px 0px;
}

.list-group-item {
  padding: .8rem .75rem;
}

#cv-btn {
  font-size: 1rem!important;
  /* color: #2559a6; */
  color: white;
  background-color: #8a140e;
  background-color: var(--red-cabin);
  border: 1px solid #8a140e;
  border: 1px solid var(--red-cabin);

}

.sidebar-div {
  margin-right: 0!important;
}

@media (min-width:576px) {
  h4 {
    font-size: 1rem!important;
  }

  h5 {
    font-size: 0.8rem!important;
  }
  .card-columns {
    -webkit-column-count: 2!important;
            column-count: 2!important;
  }
  #image-div {
    border-top: 1px solid #8a140e;
    border-top: 1px solid var(--red-cabin);
    border-right: 1px solid #8a140e;
    border-right: 1px solid var(--red-cabin);
    border-top-right-radius: 1px;
  }
}

@media (min-width:768px) {
  h4 {
    font-size: 1rem!important;
  }

  h5 {
    font-size: 0.8rem!important;
  }

  .sticky {
    position: -webkit-sticky!important;
    position: sticky!important;
    top: 0;
    z-index: 40;
    bottom: 10px;
  }
  .project-info {
    max-width: 80%!important;
  }

  .img-info {
    width: 70%;
  }
  .navbar-brand {
    display: none!important;
  }
}
/* Because the app is weird! */
@media (min-width:930px) {
  h4 {
    font-size: 1.1rem!important;
  }

  h5 {
    font-size: 0.9rem!important;
  }
}

@media (min-width:992px) {
  h4 {
    font-size: 1.3rem!important;
  }

  h5 {
    font-size: 1.1rem!important;
  }
  .sidebar-div {
    margin-right: -15px!important;
  }
}

@media (min-width:1200px) {
  h4 {
    font-size: 1.5rem!important;
  }

  h5 {
    font-size: 1rem!important;
  }
}

