  color: #1a58ba !important;
}
.News_pointer__2EnOk {
  width: 50px;
  height: 30px;
  font-size: 9px;
  position: relative;
  /* background: #2a59a6; */
  background-color: var(--dark-lake);
  margin-right: 19px;
  color: var(--snow-background);
}
.News_pointer__2EnOk:before {
  content: "";
  position: absolute;
  right: -14px;
  bottom: 0;
  width: 0;
  height: 0;
  border-left: 14px solid var(--dark-lake);
  border-top: 15px solid transparent;
  border-bottom: 15px solid transparent;
}

